<template>
  <div class="study-second01">
    <div class="banner"></div>
    <div class="container">
      <div v-if="!showCantainer">
        <p class="special">注: 以下内容需要输入密码才能观看</p>
        <input type="password" v-model="password" /><button @click="submit()">
          提交
        </button>
      </div>

      <div v-if="showCantainer" class="show-cantainer">
        <p>
          webStudy Details:
          <el-switch
            v-model="flag"
            active-color="#13ce66"
            inactive-color="#CCCCCC"
          >
          </el-switch>
        </p>
        <div class="content" v-for="(item, index) in webDataList" :key="index">
          <p>
            {{ item.title }}
            <i
              :class="{
                iconfont: true,
                'icon-down': item.show,
                'icon-up': !item.show,
              }"
              @click="showTable(index)"
            >
            </i>
          </p>
          <table v-if="item.show">
            <thead>
              <tr>
                <th v-for="(item, index) in theadList" :key="index">
                  {{ item }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(child, index) in item.content" :key="index">
                <td>{{ index + 1 }}</td>
                <td>{{ child.name }}</td>
                <td>
                  <a :href="child.src" target="_blank">Hit it hard</a>
                </td>
                <td>{{ child.id }}</td>
                <td>
                  <span v-if="flag">{{ child.password }}</span>
                </td>
                <td>
                  <span v-if="flag">{{ child.details }}</span>
                </td>
                <td>
                  <span v-if="flag">
                    {{ child.src }}
                  </span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <hr />
        <div class="personal-daily">
          <p v-for="(item, index) in diary.textList" :key="index">
            <span>{{ item.date }}:</span>
            <span>{{ item.text }}</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import * as web from '../js/source'
  import { diary } from '../js/diary'
  export default {
    data() {
      return {
        showCantainer: false,
        show: true,
        flag: false,
        webDataList: web.webSource,
        theadList: web.theadList,
        password: '',
        diary: diary,
      }
    },
    created() {
      this.webDataList.map((data) => {
        return Object.assign(data, { show: false })
      })
    },
    methods: {
      showTable(index) {
        const tempShow = !this.webDataList[index].show
        this.webDataList[index] = Object.assign(this.webDataList[index], {
          show: tempShow,
        })
      },
      submit() {
        const _ = this
        if (_.password === 'wa1355431') {
          _.showCantainer = true
        } else {
          alert('密码错误, 请重新输入')
          _.password = ''
          _.showCantainer = false
        }
      },
    },
  }
</script>
